
import { defineComponent } from "vue";
import { mapGetters, mapState } from "vuex";
import { ElMessage } from "element-plus";
import { chat } from "@front/api/chat";
import { event } from "@front/util_function/eventBus";
export default defineComponent({
	name: "roomChat",
	data() {
		return {
			value: "",
			cooling: false
		};
	},
	mounted() {
		event.on("message-focus", this.focus);
	},
	beforeUnmount() {
		event.off("message-focus", this.focus);
	},
	computed: {
		...mapGetters(["isStreaming", "isLogined"]),
		...mapState(["userSession", "roomProfile", "danmakuProfile"]),
		status(): any {
			if (this.cooling) {
				return {
					disabled: true,
					tips: ""
				};
			}
			if (!this.isLogined || !this.isStreaming) {
				return {
					disabled: true,
					tips: !this.isLogined
						? ""
						: ""
				};
			}
			return {
				disabled: false,
				tips: "(enter)"
			};
		}
	},
	watch: {
		status: {
			handler(n) {
				if (n.disabled) {
					this.value = "";
				}
			},
			immediate: true
		}
	},
	methods: {
		sendDanmaku() {
			if (!this.validation()) {
				return;
			}
			const msg = this.value;
			this.cooling = true;
			chat({
				userID: this.userSession.userID,
				deviceID: this.userSession.deviceID,
				serviceToken: this.userSession.serviceToken,
				data: {
					visitorId: this.userSession.userID,
					liveId: this.roomProfile.liveID,
					content: msg
				}
			});
			event.emit("send-message");
			ElMessage({
				message: "",
				duration: 1500,
				type: "success",
				offset: 60
			});
			this.value = "";
			setTimeout(() => {
				this.cooling = false;
				// @ts-ignore
				this.$refs.input.focus();
			}, 500);
		},
		focus() {
			// @ts-ignore
			this.$refs.input.focus();
		},
		addEmotion(command: string) {
			this.value += command;
			// @ts-ignore
			this.$refs.input.focus();
		},
		validation() {
			let errmsg = "";
			if (!this.value) {
				errmsg = "";
			} else if (this.value.length > 50) {
				errmsg = "50";
			}
			if (errmsg) {
				ElMessage({
					message: ": " + errmsg,
					duration: 1500,
					type: "error",
					offset: 60
				});
				return false;
			}
			return true;
		}
	}
});
